.AppDialogsItem {
  display: block;
  
}

.AppDialogsItem a {
  color: #ffffff;
  text-decoration: none;
  display: block;
  padding: 10px 25px;
  background: #3c4043;
  margin-bottom: 10px;
  display: flex;
  align-items: center;
  border-radius: 100px 20px 100px 20px;
}

.AppDialogsItem a:hover {
  box-shadow: 0 0 10px 3px #fff;
}

.AppDialogsItem a:active {
  box-shadow: 0 0 3px 1px #fff;
}